body {
  margin: 0;
  padding: 0;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background: linear-gradient(to right, #141e30, #243b55);
  color: #fff;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.container {
  background: rgba(0, 0, 0, 0.75);
  padding: 40px;
  border-radius: 16px;
  width: 90%;
  max-width: 600px;
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.5);
  text-align: center;
  animation: fadeIn 1s ease-in-out;
}

.title-text-light {
  font-size: 2.5rem;
  margin-bottom: 20px;
  color: #00ffe5;
  border-bottom: 2px solid #00ffe5;
  display: inline-block;
  padding-bottom: 8px;
}

ol {
  text-align: left;
  padding-left: 20px;
  line-height: 1.8;
  margin-bottom: 30px;
}

input[type="text"] {
  padding: 12px;
  width: 80%;
  font-size: 1rem;
  border: none;
  border-radius: 8px;
  margin-bottom: 20px;
  background: #f4f4f4;
  color: #333;
  transition: 0.3s;
}

input[type="text"]:focus {
  outline: none;
  box-shadow: 0 0 8px #00ffe5;
}

.start {
  margin-top: 20px;
}

.btn {
  display: inline-block;
  padding: 12px 24px;
  background-color: #00ffe5;
  color: #000;
  font-weight: bold;
  border-radius: 10px;
  text-decoration: none;
  transition: background-color 0.3s, transform 0.2s;
  margin-right: 10px; 
}


.btn:hover {
  background-color: #00d6bf;
  transform: scale(1.05);
}

#form{
    display: flex;
    justify-content:center;
    margin-top:20px;

}
 
#form.userid{
    paddidng: 7px 2px;
    width:80%;
    border: none;
    boeder-radius: 8px;
    font-size: 1rem;

}
.prev-btn {
  background-color: #ffa07a; 
}

.next-btn {
  background-color: #7fffd4; 
}


.prev-btn:hover {
  background-color: #ffff00; 
}

.next-btn:hover {
  background-color: #00ff00; 
}
.text-light {
  color: #fff; 
  font-weight: bold;
}
ul {
  list-style-type: none;  
  padding-left: 0;       
  margin-left: 0;       
}