.questions {
  margin: 20px 0;
}

.questions h2 {
  margin-bottom: 16px;
  font-weight: bold;
  color: #00ffe5;
}



.questions ul li {
  display: flex;
  align-items: center;
  margin-bottom: 0;
}

.questions input[type="radio"] {
  margin-right: 8px;
  cursor: pointer;
  width: 18px;
  height: 18px;
}

.questions label {
  cursor: pointer;

  font-weight: 600;
  user-select: none;
}

.check.checked {
 
  width: 16px;
  height: 16px;
  border-radius: 50%;
  
  margin-left: 8px;
}
